<template>
    <div class="selector-bank">
        <better-scroll ref="bankList" :data="bankList" :probeType=" probeType " 
            :listenScroll=" listenScroll "  @scroll="bankListScroll" > 
            <div class="payee-content">
                <search-bar></search-bar>
                <div class="bank-list">
                    <template v-if=" list.length > 0 " v-for="( list, section ) in bankListSection">
                        <h4 class="division" key=" section " :data-anchor=" section " :class="{ hot: section === '热' }" >{{section === '热' ? '热门银行' : section }}</h4>
                        <ul v-if=" section == '热' " class="hot-list" :class=" 'row-' + Math.ceil(bankListSection[section].length  / 4)" >
                            <li class="item" v-for=" ( item, index ) in  bankListSection[section] " >
                                <a href="javascript:;" @click="bankClick( item )">
                                    <i class="logo icon-bank-ahrcu"></i>
                                    <cite>{{ item.name }}</cite>
                                </a>
                            </li>
                        </ul>
                        <ul v-else class="list" >
                            <li class="item" v-for=" ( item, index ) in  bankListSection[section] " >
                                <a href="javascript:;" @click="bankClick( item )">
                                    <i class="logo icon-bank-ahrcu"></i>
                                    <cite>{{ item.name }}</cite>
                                </a>
                            </li>
                        </ul>
                    </template>
                </div>
            </div>
         </better-scroll>
        <div class="shortcut" ref="shortcut">
            <ul class="list">
                <li class="item" :class="[{ active : item == anchorActive }, item ]" v-for=" item in anchorList" :data-anchor="item">{{ item === 'search' ? '' : item }}</li>
            </ul>
        </div>
    </div>
</template>

<script>

import { mapGetters, mapActions } from 'vuex';

import * as services from '@/services/bank/selector';
import searchBar from './search-bar';

export default {
    data() {
        return {
            routerFrom: '',
            probeType: 3,
            listenScroll: true,
            bankList: [],
            bankListSection: [],
            anchorList: [],
            anchorMap: {},
            anchorActive: '',
            bswHeight: 0,
            bscHeight: 0,
            search: '',
            divisionHeight: 76,
            hotDivisionHeight: 65,
            itemHeight: 100,
            hotItemHeight: 182,
            anchorHeight: 30,
            searchBarHeight: 92
        };
    },
    created(){
        services.getBankList({ id: 1, showLoading: '请等待...' }).then((res) => {
            let _temp = res.data.hotlist.concat( res.data.list );
            _temp = _temp.sort((a, b) => {
                let _x = a.cnName.substr(0, 1);
                let _y = b.cnName.substr(0, 1);
                let _t = ( _x === _y ) ? 0 : ( _x < _y ? -1 : 1 );
                return _t;
            });

            let _listSection = { 'search': [], '热': [] };
            let _anchorList = [ 'search', '热' ];
            _temp.forEach((item, index) => {
                if( item.isHot ) {
                    _listSection['热'].push( item );
                }
                let _x = item.cnName.substr(0, 1).toUpperCase();
                if( !(_x in _listSection) ){
                    _listSection[ _x ] = [];
                    _anchorList.push( _x );
                }
                _listSection[ _x ].push( item );
            });
            let _y = 0;
            let _divisionHeight = this.$flexible.convert( this.divisionHeight );
            let _itemHeight = this.$flexible.convert( this.itemHeight );
            let _hotItemHeight = this.$flexible.convert( this.hotItemHeight );
            let _searchBarHeight = this.$flexible.convert( this.searchBarHeight );
            let _hotDivisionHeight = this.$flexible.convert( this.hotDivisionHeight );
            let _fix = _temp.length / 1800;
            for(var item in _listSection ){
                this.anchorMap[ item ] = _y;
                if( item == 'search' ) {
                    _y -= _searchBarHeight;
                } else if ( item === '热' ) {
                    _y -= _hotDivisionHeight + Math.ceil(_listSection[item].length / 4 ) * _hotItemHeight;
                } else {
                    _y -= _divisionHeight + _listSection[item].length * _itemHeight;
                }
                _y += _fix;
            }
            console.log(this.anchorMap);
            this.bankList = [ 1 ];
            this.bankListSection = _listSection;
            this.anchorList = _anchorList;
        }, (err) => {
            console.log( err );
        });
    },
    mounted (){
        this.bswHeight = document.querySelector('.better-scroll-wrapper').clientHeight;
        let _firstTouch;
        let _touch = {};
        let _anchorHeight = this.$flexible.convert( this.anchorHeight );
        this.$refs.shortcut.addEventListener('touchstart', (e) => {
            this.bscHeight = document.querySelector('.bank-list').clientHeight;
            let _anchor = e.target.getAttribute('data-anchor');
		    _firstTouch = e.touches[0];
		    _touch.y1 = _firstTouch.pageY;
		    _touch.anchor = _anchor;
            this._scrollTo( _anchor );
        });
        this.$refs.shortcut.addEventListener('touchmove', (e) => {
            _firstTouch = e.touches[0];
		    _touch.y2 = _firstTouch.pageY;
		    let _delta = (_touch.y2 - _touch.y1) / _anchorHeight | 0;
		    let _anchor = this.anchorList[ this.anchorList.indexOf( _touch.anchor ) + _delta ];
            this._scrollTo( _anchor );
		    e.preventDefault();
		    e.stopPropagation();
        });
    },
    computed: {
    },
    components: {
        searchBar
    },
    methods: {
        _anchorNext ( key ) {
            return this.anchorList[ this.anchorList.indexOf( key ) + 1 ];
        },
        _currentAnchor ( y ) {
            for (var i in this.anchorMap) {
                let _anchorNext = this._anchorNext( i ) || -1;
                if( y < this.anchorMap[i] && ( y > this.anchorMap[ _anchorNext ] || _anchorNext === -1 ) ) {
                    this.anchorActive = i;
                }
            }
        },
        _scrollTo ( anchor ) {
            // this.search = anchor;
            let _maxY = this.bswHeight - this.bscHeight;
		    let _y = Math.min(0, Math.max(_maxY, this.anchorMap[ anchor ]));
            this.$refs.bankList.scrollTo(0, _y );
            this.anchorActive = anchor;
	    },
        bankClick ( data ) {
            this.$store.dispatch('updateBank', data);
            this.$router.replace({ path: this.routerFrom.path });
        },
        bankListScroll (data) {
            this._currentAnchor( data.y );
        }
    },
    beforeRouteEnter (to, from, next) {
        next((vm)=>{
            vm.routerFrom = from;
        });
    }
};
</script>



<style lang="scss" scoped>
    
    .selector-bank
    {
        position: relative;
        height: 100%;
        background-color: #F7F7F7;

        .better-scroll-wrapper
        {
            height: 100%;
            overflow: hidden;
        }
    }


    .bank-list
    {
        position: relative;
        height: 100%;
        .division
        {
            padding-left: 58px;
            margin: 0px;
            height: 76px;
            line-height: 76px;
            background-color: #F7F7F7;
            font-size: 15px;
            color: #FF942F;

            &.hot
            {
                position: relative;
                padding-left: 70px;
                height: 50px;
                line-height: 36px;
                color: #272727;
                font-size: 13px;
                &:before
                {
                    content: '';
                    position: absolute;
                    left: 50px;
                    top: 6px;
                    height: 20px;
                    width: 4px;
                    background-color: #FF942F;;
                }
            }
        }
        .list
        {
            .item
            {
                background-color: #fff;
                a
                {
                    position: relative;
                    display: block;
                    padding: 0px 0px 0px 144px;
                    height: 100px;
                    line-height: 100px;
                    overflow: hidden;
                    @include mini-border(top);

                    &:before
                    {
                        left: 50px;
                    }

                    cite
                    {
                        font-size: 16px;
                        color: #272727;
                    }

                    .logo
                    {
                        left: 50px;
                    }
                }
            }

            .item:first-child
            {
                a
                {
                    &:before
                    {
                        content: none;
                    }
                }
            }
        }

        .hot-list
        {
            padding: 0px 50px 0px 0px;
            background-color: #fff;
            @include clearfix();
            .item
            {
                float: left;
                position: relative;
                width: 25%;

                @include mini-border(bottom, #d9d9d9, before);
                @include mini-border(right, #d9d9d9, after);

                a
                {
                    position: relative;
                    display: block;
                    padding-top: 114px;
                    height: 182px;
                    text-align: center;
                    overflow: hidden;

                    @include ellipsis();

                    .logo
                    {
                        position: absolute;
                        top: 40px;
                        left: 50%;
                        width: 60px;
                        height: 60px;
                        @include transform(translateX(-50%));
                    }
                    cite
                    {
                        display: block;
                        font-size: 13px;
                        color: #444444;

                        @include ellipsis();
                    }
                }
            }

            // .item + .item
            // {
            //     margin-top: 2px;
            // }

            .item:nth-of-type(4n+4)
            {
                &:after
                {
                    content: none;
                }   
            }
            @for $i from 1 through 10 
            {
                &.row-#{$i}
                {
                    .item:nth-of-type(n+#{$i*4-3})
                    {
                        &:before
                        {
                            content: none;
                        }
                    }
                }
            }
        }


    }

    .shortcut
    {
        position: fixed;
        top: 374px;
        right: 0px;
        z-index: 9;

        .list
        {
            .item
            {
                height: 30px;
                line-height: 30px;
                width: 50px;
                text-align: center;
                font-size: 13px;
                color: #B5B5B5;
                &.active
                {
                    color: #B5B5B5;
                }
            }
            .item.search
            {
                position: relative;
                height: 40px;
                &:before
                {
                    content: '';
                    position: absolute;
                    top: 50%;
                    left: 53%;
                    width: 40px;
                    height: 40px;
                    @include transform(translate(-50%, -50%));
                    @include image-set('../../assets/img/selector/icon-search');
                    background-position: center;
                }
            }
        }
    }
    
</style>



