<template>
    <div>
        <div class="field-part">
            <div class="field-group">
                <mt-field label="户名" placeholder="收款人"  v-model=" dataForm.payeeName " disableClear>
                </mt-field>
                <mt-field label="账号" placeholder="收款账号" type="number" v-model=" dataForm.payeeAccount " disableClear></mt-field>
                <router-link to="/selector/payee" class="btn-selector btn-seletor-payee">
                    <i class="icon"></i>
                </router-link>
            </div>
