        <div class="field-part">
            <mt-cell title="银行" href="#/selector/bank"  :value=" dataForm.bankName || '请选择银行' " is-link>
            </mt-cell>
        </div>
        <div class="field-part">
            <mt-cell title="开户地址" href="#/selector/bank-city"  :value=" dataForm.bankCity || '请选择开户地址' " is-link>
            </mt-cell>
