            <mt-cell title="支行" href="#/selector/bank-branch"  :value=" dataForm.bankBranch || '请选择支行' " is-link>
            </mt-cell>
        </div>
    </div>
</template>
<script>

    import { mapGetters, mapActions } from 'vuex';

    export default {
        data () {
            return {
                dataForm: {
                    payeeName: '',
                    payeeAccount: '',
                    payeeBankName: '',
                    bankName: '',
                    bankCity: ''
                }
            };
        },
        created() {
            this.dataForm = Object.assign({}, this.$store.getters.transactionDemo);
        },
        methods: {
        }
    };
</script>

<style lang="scss" scoped>
    .btn-seletor-payee
    {
        background-color: #fff;
        .icon
        {
            background-image: url('../../assets/img/selector/icon-address-book.png');
        }
    }
</style>


