<template>
    <div class="selector-bank-city">
        <better-scroll ref="bankCityList" :data="bankCityList" :probeType=" probeType " 
        :listenScroll=" listenScroll "  :bounce="bounce" 
        @scroll="bankCityListScroll" > 
            <div class="payee-content">
                <search-bar></search-bar>
                <div class="bank-city-list">
                    <template v-if=" list.length > 0 " v-for="( list, section ) in bankCityListSection">
                        <h4 class="division" key=" section " :data-anchor=" section " :class="{ hot: section === '热' }" >{{section === '热' ? '热门城市' : section }}</h4>
                        <ul v-if=" section == '热' " class="hot-list" >
                            <li class="item" v-for=" ( item, index ) in  bankCityListSection[section] " >
                                <a href="javascript:;" @click="bankClick( item )">
                                    <cite>{{ item.name }}</cite>
                                </a>
                            </li>
                        </ul>
                        <ul v-else class="list" >
                            <li class="item" v-for=" ( item, index ) in  bankCityListSection[section] " >
                                <a href="javascript:;" @click="bankClick( item )">
                                    <cite>{{ item.name }}</cite>
                                </a>
                            </li>
                        </ul>
                    </template>
                </div>
            </div>
        </better-scroll>
        <div class="shortcut" ref="shortcut">
            <ul class="list">
                <li class="item" :class="[{ active : item == anchorActive }, item ]" v-for=" item in anchorList" :data-anchor="item">{{ item === 'search' ? '' : item }}</li>
            </ul>
        </div>
    </div>
</template>

<script>

import { mapGetters, mapActions } from 'vuex';

import * as services from '@/services/bank/selector';
import searchBar from './search-bar';

export default {
    data() {
        return {
            routerFrom: '',
            searchFocus: false,
            probeType: 3,
            bounce: false,
            listenScroll: true,
            bankCityList: [],
            bankCityListSection: [],
            anchorList: [],
            anchorMap: {},
            anchorActive: '',
            bswHeight: 0,
            bscHeight: 0,
            divisionHeight: 76,
            hotDivisionHeight: 55,
            itemHeight: 100,
            hotItemHeight: 104,
            anchorHeight: 30,
            searchBarHeight: 92
        };
    },
    created(){
        services.getBankCityList({ id: 1, showLoading: '请等待...' }).then((res) => {
            let _temp = res.data.hotlist.concat( res.data.list );
            _temp = _temp.sort((a, b) => {
                let _x = a.cnName.substr(0, 1);
                let _y = b.cnName.substr(0, 1);
                let _t = ( _x === _y ) ? 0 : ( _x < _y ? -1 : 1 );
                return _t;
            });

            let _listSection = { 'search': [], '热': [] };
            let _anchorList = [ 'search', '热' ];
            _temp.forEach((item, index) => {
                if( item.isHot ) {
                    _listSection['热'].push( item );
                }
                let _x = item.cnName.substr(0, 1).toUpperCase();
                if( !(_x in _listSection) ){
                    _listSection[ _x ] = [];
                    _anchorList.push( _x );
                }
                _listSection[ _x ].push( item );
            });
            let _y = 0;
            let _divisionHeight = this.$flexible.convert( this.divisionHeight );
            let _hotDivisionHeight = this.$flexible.convert( this.hotDivisionHeight );
            let _itemHeight = this.$flexible.convert( this.itemHeight );
            let _hotItemHeight = this.$flexible.convert( this.hotItemHeight );
            let _hotFixHeight = this.$flexible.convert( 40 );
            let _searchBarHeight = this.$flexible.convert( this.searchBarHeight );
            let _fix = _temp.length / 3000;
            for(var item in _listSection ){
                this.anchorMap[ item ] = _y;
                if( item == 'search' ) {
                    _y -= _searchBarHeight;
                } else if( item == '热' ) {
                    _y -= _hotDivisionHeight + _hotFixHeight + Math.ceil(_listSection[item].length / 4 ) * _hotItemHeight;
                } else {
                    _y -= _divisionHeight + _listSection[item].length * _itemHeight;
                }
                _y += _fix;
            }
            this.bankCityList = [ 1 ];
            this.bankCityListSection = _listSection;
            this.anchorList = _anchorList;
        }, (err) => {
            console.log( err );
        });
    },
    mounted (){
        this.bswHeight = document.querySelector('.better-scroll-wrapper').clientHeight;
        let _firstTouch;
        let _touch = {};
        let _anchorHeight = this.$flexible.convert( this.anchorHeight );
        this.$refs.shortcut.addEventListener('touchstart', (e) => {
            this.bscHeight = document.querySelector('.bank-city-list').clientHeight;
            let _anchor = e.target.getAttribute('data-anchor');
		    _firstTouch = e.touches[0];
		    _touch.y1 = _firstTouch.pageY;
		    _touch.anchor = _anchor;
            this._scrollTo( _anchor );
        });
        this.$refs.shortcut.addEventListener('touchmove', (e) => {
            _firstTouch = e.touches[0];
		    _touch.y2 = _firstTouch.pageY;
		    let _delta = (_touch.y2 - _touch.y1) / _anchorHeight | 0;
		    let _anchor = this.anchorList[ this.anchorList.indexOf( _touch.anchor ) + _delta ];
            this._scrollTo( _anchor );
		    e.preventDefault();
		    e.stopPropagation();
        });
    },
    computed: {
    },
    components: {
        searchBar
    },
    methods: {
        _anchorHeight () {
            let flexible = window['lib'].flexible;
            return ( flexible.rem / flexible.standard ) * 32;
        },
        _anchorNext ( key ) {
            return this.anchorList[ this.anchorList.indexOf( key ) + 1 ];
        },
        _currentAnchor ( y ) {
            for (var i in this.anchorMap) {
                let _anchorNext = this._anchorNext( i ) || -1;
                if( y < this.anchorMap[i] && ( y > this.anchorMap[ _anchorNext ] || _anchorNext === -1 ) ) {
                    this.anchorActive = i;
                }
            }
        },
        _scrollTo ( anchor ) {
            // this.search = anchor;
            let _maxY = this.bswHeight - this.bscHeight;
		    let _y = Math.min(0, Math.max(_maxY, this.anchorMap[ anchor ]));
            this.$refs.bankCityList.scrollTo(0, _y );
            this.anchorActive = anchor;
	    },
        bankClick ( data ) {
            this.$store.dispatch('updateBankCity', data);
            this.$router.replace({ path: this.routerFrom.path });
        },
        bankCityListScroll (data) {
            this._currentAnchor( data.y );
        },
        searchFocusToggle (val) {
            this.searchFocus = val;
        }
    },
    beforeRouteEnter (to, from, next) {
        next((vm)=>{
            vm.routerFrom = from;
        });
    }
};
</script>



<style lang="scss" scoped>
    
    .selector-bank-city
    {
        position: relative;
        height: 100%;
        background-color: #F7F7F7;

        .better-scroll-wrapper
        {
            height: 100%;
            overflow: hidden;
        }
    }
    .bank-city-list
    {
        .division
        {
            padding-left: 58px;
            margin: 0px;
            height: 76px;
            line-height: 76px;
            background-color: #F7F7F7;
            font-size: 15px;
            color: #FF942F;

            &.hot
            {
                position: relative;
                padding-left: 70px;
                height: 50px;
                line-height: 36px;
                color: #272727;
                font-size: 13px;
                &:before
                {
                    content: '';
                    position: absolute;
                    left: 50px;
                    top: 6px;
                    height: 20px;
                    width: 4px;
                    background-color: #FF942F;;
                }
            }
        }
        .list
        {
            .item
            {
                a
                {
                    position: relative;
                    display: block;
                    padding: 0px 0px 0px 50px;
                    height: 100px;
                    line-height: 100px;
                    overflow: hidden;
                    background-color: #fff;

                    @include mini-border(top, #F1F0F3);
                    &:before
                    {
                        left: 50px;
                    }

                    .icon
                    {

                    }

                    cite
                    {
                        font-size: 16px;
                        color: #272727;
                    }
                }
            }

            .item:first-child
            {
                a
                {
                    &:before
                    {
                        content: none;
                    }
                }
            }
        }

        .hot-list
        {
            padding: 40px 50px 0px 18px;
            background-color: #fff;
            @include clearfix();
            .item
            {
                float: left;
                margin-bottom: 40px;
                width: 25%;
                height: 60px;
                a
                {
                    position: relative;
                    display: block;
                    margin: 0px 12px;
                    padding: 0px 5px;
                    height: 60px;
                    text-align: center;

                    @include mini-border(all, #999999, after, 12px);

                    cite
                    {
                        display: block;
                        font-size: 15px;
                        height: 60px;
                        line-height: 66px;
                        color: #666666;

                        @include ellipsis();
                    }
                }
            }

            .item:nth-of-type(4n+4)
            {
                &:after
                {
                    content: none;
                }   
            }
        }

    }

    .shortcut
    {
        position: fixed;
        top: 168px;
        right: 0px;
        z-index: 9;

        .list
        {
            .item
            {
                height: 30px;
                line-height: 30px;
                width: 50px;
                text-align: center;
                font-size: 13px;
                color: #B5B5B5;
                &.active
                {
                    color: #B5B5B5;
                }
            }
            .item.search
            {
                position: relative;
                height: 40px;
                &:before
                {
                    content: '';
                    position: absolute;
                    top: 50%;
                    left: 53%;
                    width: 40px;
                    height: 40px;
                    @include transform(translate(-50%, -50%));
                    @include image-set('../../assets/img/selector/icon-search');
                    background-position: center;
                }
            }
        }
    }
    
</style>



