<template>
    <div class="selector-payee">
        <better-scroll ref="historyPayeeList" :data="latelyPayeeList" 
            :probeType=" probeType " :listenScroll=" listenScroll " :bounce="bounce" 
            class="better-scroll-history-payee"
            @scroll="historyPayeeListScroll" > 
            <div class="payee-content">
                <search-bar></search-bar>
                <div class="lately-payee">
                    <h3 class="title">最近为他们还款</h3>
                    <better-scroll :data="latelyPayeeList" :scrollX="scrollX" :bounce="bounce" >
                        <ul class="payee-list" :style="{ width: latelyPayeeListWidth }"  >
                            <li class="item" v-for=" ( item, index) in latelyPayeeList ">
                                <a href="javascript:;" @click="payeeClick( item )" >
                                    <i class="logo icon-bank-ahrcu"></i>
                                    <cite>{{ item.name }}</cite>
                                    <span>{{ item.bankName }}</span>
                                    <span>{{ item.account }}</span>
                                </a>
                            </li>
                        </ul>
                    </better-scroll>
                </div>

                <div class="history-payee">
                    <h3 class="title">历史为他们还款</h3>
                    <div class="payee-list">
                        <template v-if=" list.length > 0 " v-for="( list, section ) in historyPayeeList">
                            <h4 class="division" key=" section " :data-anchor=" section " >{{ section }}</h4>
                            <ul class="list">
                                <li class="item" v-for=" ( item, index ) in list " >
                                    <a href="javascript:;" @click="payeeClick( item )">
                                        <i class="logo icon-bank-ahrcu"></i>
                                        <div class="attr">
                                            <cite>{{ item.name }}</cite>
                                            <span>{{ item.account }}</span>
                                            <span>{{ item.bankName }}</span>
                                        </div>
                                    </a>
                                </li>
                            </ul>
                        </template>
                    </div>
                </div>
            </div>
        </better-scroll>

        <div class="shortcut" ref="shortcut">
            <ul class="list">
                <li class="item" :class="[{ active : item == anchorActive }, item ]" v-for=" item in anchorList" :data-anchor="item">{{ item === 'search' ? '' : item }}</li>
            </ul>
        </div>
    </div>
</template>

<script>

import { mapGetters, mapActions } from 'vuex';

import * as services from '@/services/bank/selector';
import searchBar from './search-bar';

export default {
    data() {
        return {
            routerFrom: '',
            scrollX: true,
            probeType: 3,
            bounce: false,
            listenScroll: true,
            payeeList: [],
            latelyPayeeList: [],
            historyPayeeList: [],
            anchorList: [],
            latelyPayeeListWidth: '0px',
            anchorMap: {},
            anchorActive: 'search',
            bswHeight: 0,
            bscHeight: 0,
            divisionHeight: 76,
            itemHeight: 174,
            anchorHeight: 30,
            searchBarHeight: 86,
            latelyPayeeHeight: 315
        };
    },
    created(){
        services.getPayeeList({ id: 1, showLoading: '请等待...' }).then((res) => {
            let _temp = res.data.hotlist.concat( res.data.list );
            _temp = _temp.sort((a, b) => {
                let _x = a.cnName.substr(0, 1);
                let _y = b.cnName.substr(0, 1);
                let _t = ( _x === _y ) ? 0 : ( _x < _y ? -1 : 1 );
                return _t;
            });
            let _latelyPayeeList = _temp.filter((item) => { return item.isLately === true });

            this.latelyPayeeListWidth = this.$flexible.convert( _latelyPayeeList.length * 330 + 30 ) + 'px';
            this.latelyPayeeList = _latelyPayeeList;

            let _listSection = { 'search': [], '近': [] };
            let _anchorList = [ 'search', '近' ];
            _temp.forEach((item, index) => {
                let _x = item.cnName.substr(0, 1).toUpperCase();
                if( !(_x in _listSection) ){
                    _listSection[ _x ] = [];
                    _anchorList.push( _x );
                }
                _listSection[ _x ].push( item );
            });
            let _y = 0;
            let _divisionHeight = this.$flexible.convert( this.divisionHeight );
            let _itemHeight = this.$flexible.convert( this.itemHeight );
            let _searchBarHeight = this.$flexible.convert( this.searchBarHeight );
            let _latelyPayeeHeight = this.$flexible.convert( this.latelyPayeeHeight );
            let _fix = _temp.length / 2400;
            for(var item in _listSection ){
                this.anchorMap[ item ] = _y;
                if( item == 'search' ) {
                    _y -= _searchBarHeight;
                } else if( item == '近' ) {
                    _y -= _latelyPayeeHeight;
                } else {
                    _y -= _divisionHeight + _listSection[item].length * _itemHeight;
                }
                _y += _fix;
            }
            this.historyPayeeList = _listSection;
            this.anchorList = _anchorList;
        }, (err) => {
            console.log( err );
        });
    },
    mounted (){
        this.bswHeight = document.querySelector('.better-scroll-history-payee').clientHeight;
        let _firstTouch;
        let _touch = {};
        let _anchorHeight = this.$flexible.convert( this.anchorHeight );
        this.$refs.shortcut.addEventListener('touchstart', (e) => {
            this.bscHeight = document.querySelector('.better-scroll-history-payee .payee-content').clientHeight;
            let _anchor = e.target.getAttribute('data-anchor');
		    _firstTouch = e.touches[0];
		    _touch.y1 = _firstTouch.pageY;
		    _touch.anchor = _anchor;
            this._scrollTo( _anchor );
        });
        this.$refs.shortcut.addEventListener('touchmove', (e) => {
            _firstTouch = e.touches[0];
		    _touch.y2 = _firstTouch.pageY;
		    let _delta = (_touch.y2 - _touch.y1) / _anchorHeight | 0;
		    let _anchor = this.anchorList[ this.anchorList.indexOf( _touch.anchor ) + _delta ];
            this._scrollTo( _anchor );
		    e.preventDefault();
		    e.stopPropagation();
        });
    },
    computed: {
    },
    components: {
        searchBar
    },
    methods: {
        _latelyPayeeListWidth ( length ) {
            let flexible = window['lib'].flexible;
            return ( flexible.rem / flexible.standard ) * length * 355 + 'px';
        },
        _anchorNext ( key ) {
            return this.anchorList[ this.anchorList.indexOf( key ) + 1 ];
        },
        _currentAnchor ( y ) {
            for (var i in this.anchorMap) {
                let _anchorNext = this._anchorNext( i ) || -1;
                if( y < this.anchorMap[i] && ( y > this.anchorMap[ _anchorNext ] || _anchorNext === -1 ) ) {
                    this.anchorActive = i;
                }
            }
        },
        _scrollTo ( anchor ) {
            // this.search = anchor;
            let _maxY = this.bswHeight - this.bscHeight;
		    let _y = Math.min(0, Math.max(_maxY, this.anchorMap[ anchor ]));
            this.$refs.historyPayeeList.scrollTo(0, _y );
            this.anchorActive = anchor;
	    },
        payeeClick ( data ) {
            this.$store.dispatch('updatePayee', data);
            this.$router.replace({ path: this.routerFrom.path });
        },
        historyPayeeListScroll (data) {
            this._currentAnchor( data.y );
        }
    },
    beforeRouteEnter (to, from, next) {
        next((vm)=>{
            vm.routerFrom = from;
        });
    }
};
</script>



<style lang="scss" scoped>
    
    .selector-payee
    {
        position: relative;
        height: 100%;
        background-color: #F7F7F7;

        .better-scroll-wrapper
        {
            height: 100%;
            overflow: hidden;
        }
    }
    .title
    {
        position: relative;
        padding-left: 50px;
        width: 100%;
        margin: 0px;    
        font-size: 13px;
        color: #272727;
        line-height: 32px;
        font-weight: bold;
        &:before
        {
            content: '';
            position: absolute;
            left: 40px;
            top: 6px;
            height: 20px;
            width: 4px;
            background-color: #FF942F;;
        }
    }

    .lately-payee,
    .history-payee
    {
        position: relative;
    }
    .lately-payee
    {
        position: relative;
        width: 100%;
        height: 248px;
        overflow: hidden;

        .better-scroll-wrapper
        {
            padding-top: 25px;
            margin-top: 20px;
            height: 196px;
            background-color: #fff;
        }
        
        .payee-list
        {
            padding-top: 5px;
            overflow-x: auto;
            white-space: nowrap;
            height: 150px;
            
            background-color: #fff;
            .item
            {
                display: inline-block;
                a
                {
                    position: relative;
                    display: block;
                    padding: 14px 0px 0px 20px;
                    height: 136px;
                    width: 300px;

                    box-shadow: 0 2px 5px 0px rgba(207,207,207,0.50), 0px 0px 1px 0px rgba(207,207,207,0.30);
                    border-radius: 4px;

                    cite,
                    span
                    {
                        display: block;
                        margin-bottom: 4px;
                        font-size: 15px;
                        color: #272727;
                        line-height: 36px;
                    }
                    span
                    {
                        font-size: 13px;
                        color: #B5B5B5;
                    }
                    span + span
                    {
                        font-size: 13px;
                        color: #272727;
                    }

                    .logo
                    {
                        position: absolute;
                        top: 0px;
                        left: auto;
                        right: 0px;
                        z-index: -1;
                        height: 136px;
                        width: 120px;
                        opacity: 0.1;

                        @include transform(translate(0px, 0px));
                        background-size: 160px 160px;
                        background-position: left center;
                    }
                }
            }

            .item
            {
                margin-left: 30px;
            }
        }
    }
    .history-payee
    {
        padding-left: 0px;
        padding-right: 0px;
        height: 100%;
        .title
        {
            margin-top: 16px;
        }

        .better-scroll-wrapper
        {
            position: relative;
            height: 100%;
            overflow: hidden;
        }

        .payee-list
        {
            .division
            {
                padding-left: 58px;
                margin: 0px;
                height: 76px;
                line-height: 76px;
                background-color: #F7F7F7;
                font-size: 15px;
                color: #FF942F;
            }
            .item
            {
                a
                {
                    position: relative;
                    display: block;
                    padding: 20px 0px 0px 144px;
                    height: 174px;
                    overflow: hidden;
                    background-color: #fff;

                    @include mini-border(top, #F1F0F3);
                    &:before
                    {
                        left: 50px;
                    }

                    .logo
                    {
                        position: absolute;
                        top: 50%;
                        left: 50px;
                        width: 64px;
                        height: 64px;

                    }
                    .attr
                    {
                        cite,span
                        {
                            display: block;
                            line-height: 44px;
                            margin-bottom: 4px;
                        }
                        cite
                        {
                            font-size: 16px;
                            color: #272727;
                        }
                        span
                        {
                            
                            font-size: 16px;
                            color: #272727;
                        }
                        span + span
                        {
                            line-height: 36px;
                            font-size: 13px;
                            color: #B5B5B5;
                        }
                    }
                }
            }

            .item:first-child
            {
                a
                {
                    &:before
                    {
                        content: none;
                    }
                }
            }

        }
    }

    .shortcut
    {
        position: fixed;
        top: 374px;
        right: 0px;
        z-index: 9;

        .list
        {
            .item
            {
                height: 30px;
                line-height: 30px;
                width: 64px;
                text-align: center;
                font-size: 13px;
                color: #B5B5B5;
                &.active
                {
                    color: #B5B5B5;
                }
            }
            .item.search
            {
                position: relative;
                height: 40px;
                &:before
                {
                    content: '';
                    position: absolute;
                    top: 50%;
                    left: 53%;
                    width: 40px;
                    height: 40px;
                    @include transform(translate(-50%, -50%));
                    @include image-set('../../assets/img/selector/icon-search');
                    background-position: center;
                }
            }
        }
    }
    
</style>



