<template>
    <div class="search-bar">
        <div class="input-border" :class="{ 'focus': search || searchFocus }">
            <input type="text" v-model="search" placeholder="搜索" v-on:focus="searchFocusToggle(true)" v-on:blur="searchFocusToggle(false)">
            <span class="placeholder">搜索</span> 
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            search: '',
            searchFocus: false
        };
    },
    methods: {
        searchFocusToggle (val) {
            this.searchFocus = val;
        }
    }
};
</script>
<style lang="scss" scoped>
.search-bar
{
    position: relative;
    padding: 20px 30px;
    height: 104px;
    .input-border
    {
        position: relative;
        height: 64px;
        border-radius: 100px;
        background-color: #fff;

        input
        {
            position: relative;
            z-index: 9;
            display: block;
            padding: 0px 20px;
            padding-left: 60px;
            width: 100%;
            height: 64px;
            line-height: normal;
            border: none;
            border-radius: 100px;
            background-color: transparent;

            &::-webkit-input-placeholder 
            {
                font-size: 13px;
                color: #fff;
            }
        }
        .placeholder
        {
            position: absolute;
            top: 0px;
            left: 50%;
            z-index: 1;
            padding-left: 50px;
            height: 64px;
            line-height: 64px;
            font-size: 13px;
            color: #B5B5B5;
            text-align: center;
            overflow: hidden;
            @include transform(translateX(-50%));
            // @include transition(transform, 0.15s);

            &:before
            {
                content: '';
                position: absolute;
                top: 52%;
                left: 0%;
                width: 44px;
                height: 44px;
                @include transform(translateY(-50%));
                @include image-set('../../assets/img/selector/icon-search');
                background-position: center;
            }
        }

        &.focus
        {
            .placeholder
            {
                left: 10px;
                width: 44px;
                @include transform(translateX(0%));
            }
            input::-webkit-input-placeholder 
            {
                color: #B5B5B5;;
            }
        }
    }
}
</style>



