<template>
    <div class="selector-bank-branch">
        <better-scroll ref="bankBranchList" :data="bankBranchList" 
            :probeType=" probeType "
            :bounce="bounce" 
            :listenScroll=" listenScroll "  @scroll="bankBranchListScroll" > 
            <div class="payee-content">
                <search-bar></search-bar>
                <div class="bank-branch">
                    <div class="list">
                        <li class="item" v-for="( item, index ) in bankBranchList" >
                            <a href="javascript:;" @click="bankClick( item )">
                                <cite>{{ item.name }}</cite>
                                <span>{{ item.code }}</span>
                            </a>
                        </li>
                    </div>
                </div>
            </div>
        </better-scroll>
    </div>
</template>

<script>

import { mapGetters, mapActions } from 'vuex';

import * as services from '@/services/bank/selector';
import searchBar from './search-bar';

export default {
    data() {
        return {
            routerFrom: '',
            searchFocus: false,
            probeType: 3,
            bounce: false,
            listenScroll: true,
            bankBranchList: [],
            bankBranchListSection: [],
            bswHeight: 0,
            bscHeight: 0,
            search: '',
            divisionHeight: 48,
            itemHeight: 80,
            hotItemHeight: 75,
            anchorHeight: 32
        };
    },
    created(){
        services.getBankBranchList({ id: 1, showLoading: '请等待...' }).then((res) => {
            let _temp = res.data.list;
            _temp.forEach( (item) => {
                item.name += '支行';
            });
             this.bankBranchList = _temp;
        }, (err) => {
            console.log( err );
        });
    },
    mounted (){
    },
    computed: {
    },
    components: {
        searchBar
    },
    methods: {
        _anchorHeight () {
            let flexible = window['lib'].flexible;
            return ( flexible.rem / flexible.standard ) * 32;
        },
        _anchorNext ( key ) {
            return this.anchorList[ this.anchorList.indexOf( key ) + 1 ];
        },
        _currentAnchor ( y ) {
            for (var i in this.anchorMap) {
                let _anchorNext = this._anchorNext( i ) || -1;
                if( y < this.anchorMap[i] && ( y > this.anchorMap[ _anchorNext ] || _anchorNext === -1 ) ) {
                    this.anchorActive = i;
                }
            }
        },
        _scrollTo ( anchor ) {
            // this.search = anchor;
            let _maxY = this.bswHeight - this.bscHeight;
		    let _y = Math.min(0, Math.max(_maxY, this.anchorMap[ anchor ]));
            this.$refs.bankBranchList.scrollTo(0, _y );
            this.anchorActive = anchor;
	    },
        bankClick ( data ) {
            this.$store.dispatch('updateBankBranch', data);
            this.$router.replace({ path: this.routerFrom.path });
        },
        bankBranchListScroll (data) {
            this._currentAnchor( data.y );
        },
        searchFocusToggle (val) {
            this.searchFocus = val;
        }
    },
    beforeRouteEnter (to, from, next) {
        next((vm)=>{
            vm.routerFrom = from;
        });
    }
};
</script>



<style lang="scss" scoped>
    
    .selector-bank-branch
    {
        position: relative;
        height: 100%;
        background-color: #F7F7F7;

        .better-scroll-wrapper
        {
            height: 100%;
            overflow: hidden;
        }
    }
    .bank-branch
    {
        position: relative;
        padding-bottom: 10px;
        height: 100%;
        background-color: #fff;

        .list
        {
            .item
            {
                a
                {
                    position: relative;
                    display: block;
                    padding: 30px 0px 0px 50px;
                    height: 146px;
                    overflow: hidden;

                    @include mini-border(top, #F1F0F3);
                    &:before
                    {
                        left: 50px;
                    }

                    cite
                    {
                        display: block;
                        font-size: 16px;
                        color: #272727;
                        line-height: 40px;
                    }
                    span
                    {
                        display: block;
                        margin-top: 10px;
                        font-size: 13px;
                        color: #666666;
                        line-height: 36px;
                    }
                }
            }

            .item:first-child
            {
                &:before
                {
                    content: none;
                }
            }

        }
    }
    
</style>



